  }

  .sub {
    font-weight: $font-weight-medium;
    position: relative;
    display: inline-block;
    @include font-vw(18, 14);
    @include padding-left-vw(15, 0);
    @include padding-right-vw(15, 15);
    @include sp-only {
    }
    &::before {
      content: "";
      display: block;
      position: absolute;
      right: 100%;
      bottom: 0;
      background-color: $color-black;
      @include top-vw(17, 17);
      @include height-vw(1, 1);
      @include width-vw(115, 115);
      @include sp-only {
        display: none;
      }
    }
    &::after {
      content: "";
      display: block;
      position: absolute;
      left: 100%;
      bottom: 0;
      background-color: $color-black;
      @include top-vw(17, 13);
      @include height-vw(1, 1);
      @include width-vw(115, 50);
      @include sp-only {
      }
    }
  }
}

